import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { AlertExtensionsService } from '../../services/alert-extensions.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'login-page',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  alert_timeout = 3000;

  username: String;
  password: String;

  constructor(private auth: AuthService,
              private validate_service: ValidateService,
              private flash_message: FlashMessagesService,
              private alert_ext: AlertExtensionsService,
              private router: Router){}
  ngOnInit(){}

  submit(){
    const user = {
      username: this.username,
      password: this.password
    }

    if(!this.validate_service.validateLogin(user)){
      this.flash_message.show("Missing one or more fields!", {cssClass: 'warn-alert', timeout: this.alert_timeout});
      this.alert_ext.add_close_button('warn-alert');
      return false;
    }

    this.auth.login(user).subscribe(data =>{
      if((data as any).success){
        this.auth.store_user_data((data as any).token, (data as any).user);
        this.flash_message.show((data as any).msg, {cssClass: 'success-alert', timeout: this.alert_timeout});
        this.alert_ext.add_close_button('success-alert');
        this.router.navigate(['/dashboard']);
      }
      else{
        this.flash_message.show((data as any).msg, {cssClass: 'warn-alert', timeout: this.alert_timeout});
        this.alert_ext.add_close_button('warn-alert');
        this.router.navigate(['/login']);
      }
    });
  }
}
