import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ValidateService {
  constructor(){}

  validateLogin(user){
    if(user.username == undefined || user.password == undefined) { return false; }
    else{ return true; }
  }

  validateRegister(user){
    if(user.name == undefined || user.username == undefined || user.email == undefined || user.password == undefined) { return false; }
    else{ return true; }
  }

  validateEmail(email){
    var pattern = /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/;
    return pattern.test(email);
  }

  validatePassword(password){
    var numbers = /[0-9]/g;
    var lower = /[a-z]/g;
    var upper = /[A-Z]/g;
    return ((password.length > 8 && password.length < 40) && (password.match(numbers) !== null) && (password.match(lower) !== null) && (password.match(upper) !== null));
  }

  matchPasswords(password_1, password_2){
    return (password_1 === password_2);
  }
}
