import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { AlertExtensionsService } from '../../services/alert-extensions.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'navbar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.css']
})
export class NavBarComponent implements OnInit {
  title = 'DockSkid';
  alert_timeout = 3000;

  constructor(private auth: AuthService,
              private validate_service: ValidateService,
              private flash_message: FlashMessagesService,
              private alert_ext: AlertExtensionsService,
              private router: Router){}
  ngOnInit(){}

  logout(){
    if(this.auth.logout()){
      this.flash_message.show("Logged out!", {cssClass: 'success-alert', timeout: this.alert_timeout});
      this.alert_ext.add_close_button('success-alert');
      this.router.navigate(['/']);
    }
    else{
      this.flash_message.show("Not logged in!", {cssClass: 'warn-alert', timeout: this.alert_timeout});
      this.alert_ext.add_close_button('warn-alert');
    }
  }
}
